.search {
    transition: width 350ms ease-in-out;

    &.is-closed {
        width: 52px;
        overflow: hidden;

        .header__actions-item--new & {
            width: 44.67px;
        }
    }

    &--header {
        .search-open & {
            position: absolute;
            width: 100%;
            right: 0;
            z-index: $z-index-search;

            @include bp(lg-min) {
                left: auto;
                right: 52px;
                width: 700px;
            }
        }
    }
}

.search__inner {
    display: flex;
    width: 100%;

    .search--page & {
        border-bottom: 1px solid $color-dark;
    }

    .search--header & {
        justify-content: flex-end;
    }
}

.search__field {
    flex: 1 1 auto;
    position: relative;

    .search--header & {
        width: 0;
        overflow: hidden;
    }

    .search-open .search--header & {
        width: 100%;
    }
}

.search__submit {
    flex: 0 0 52px;
    height: 52px;

    .header__actions-item--new & {
        flex: 0 0 44.67px;
    }
}

.search__textfield {
    .form-textfield__label {
        font-size: $font-size-base;
        color: rgba($color-dark, .5);
        letter-spacing: normal;
    }

    .search--page & .form-textfield__label {
        color: $color-dark;
    }

    .form-textfield__input {
        font-size: 16px;

        @include bp(lg-min) {
            font-size: $font-size-base;
        }
    }
}

.search__close {
    width: 52px;
    height: 52px;
    background: transparent;
    box-shadow: none;
    border: none;
    padding: 0;
    -webkit-appearance: none;
    position: absolute;
    top: 0;
    right: 0;
    display: none;
    z-index: $z-index-search;
    font-size: 20px;

    .search--header:not(.is-closed) & {
        display: block;
    }
}

.search__button {
    padding: 0;
    width: 52px;
    height: 52px;
    display: block;

    .header__actions-item--new & {
        width: 100%;
    }
}

.search__results {
    padding: 26px 16px 0;
    background-color: #fefefe;
    opacity: 0;
    visibility: hidden;

    .search-open &,
    .search--header & {
        display: none;
    }

    &.is-open {
        opacity: 1;
        visibility: visible;
        display: block;
    }

    .search--page & {
        padding-bottom: 26px;
    }
}

.search__results-row:not(:last-child) {
    border-bottom: 1px solid $color-gray-bg;
    margin-bottom: 24px;
    padding-bottom: 10px;
}

.search__results-title {
    font-size: $font-size-tiny;
    text-transform: uppercase;
    font-weight: $font-weight-medium;
    line-height: 1.4;
    padding-top: 3px;
}

.search__results-item {
    padding-bottom: 16px;
    line-height: 1.4;
}

.search__results-link {
    color: $color-dark;
}

.search__results-all {
    font-size: $font-size-tiny;
}

.accordion__title {
    .js-accordion-control:not(.accordion__header-second) & {
        .search__results & {
            letter-spacing: 1px;
            line-height: 48px;

            @include bp(sm-min) {
                padding-left: 26px;
            }
        }
    }
}

.accordion__inner {
    .accordion--simple & {
        .search__results & {
            padding: 18px 0 36px;
        }
    }

    .accordion--light & {
        .search__results & {
            padding: 16px 0 26px;
        }
    }
}

.accordion__header:after {
    .accordion--simple & {
        .search__results & {
            @include bp(sm-min) {
                right: 30px;
            }
        }
    }
}

.accordion__header:before {
    .accordion--simple & {
        .search__results & {
            @include bp(sm-min) {
                right: 24px;
            }
        }
    }
}

.accordion__content {
    .accordion__content & {
        .search__results & {
            padding: 0 32px;

            @include bp(sm-min) {
                padding: 0 52px;
            }
        }
    }
}

.accordion--simple .accordion__item > .accordion__content {
    .search__results & {
        margin: 0 -16px;

        @include bp(sm-min) {
            margin: 0;
        }
    }
}

.accordion__item + .accordion__item {
    .search__results & {
        margin: 0;
    }
}

.search__dl-icon {
    font-size: 22px;
    color: $color-red-brand;
}

.search__tooltip {
    display: block;
    position: absolute;
    top: 65px;
    background-color: $color-black;
    color: $color-white;
    padding: 13px 45px 12px 15px;
    font-size: 13px;
    min-width: 260px;
    transform: translate3d(0, 0, 0);
    @include transition(transform, 400ms);

    &:after {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        border-style: solid;
        border-color: transparent;
        border-top: 0;
        top: -8px;
        border-bottom-color: $color-black;
        border-width: 9px;
        right: 18px;
    }

    &.is-hidden {
        transform: translate3d(150%, 0, 0);
    }
}

.search__close-tooltip {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 10px;
    margin: auto;
    font-size: 25px;
    color: #e2051b;
    cursor: pointer;
}
